import ElasticEaseIn from '../core/easing/ElasticEaseIn';
import ElasticEaseInOut from '../core/easing/ElasticEaseInOut';
import ElasticEaseOut from '../core/easing/ElasticEaseOut';
import ElasticEaseOutIn from '../core/easing/ElasticEaseOutIn';


const easeIn = new ElasticEaseIn();
const easeOut = new ElasticEaseOut();
const easeInOut = new ElasticEaseInOut();
const easeOutIn = new ElasticEaseOutIn();


export default class Elastic
{
	static get easeIn() { return easeIn; }
	static get easeOut() { return easeOut; }
	static get easeInOut() { return easeInOut; }
	static get easeOutIn() { return easeOutIn; }

	/**
	 *
	 * @param	a	Specifies the amplitude of the sine wave.
	 * @param	p	Specifies the period of the sine wave.
	 * @return	An easing with passed parameters.
	 */
	static easeInWith(a = 0, p = 0)
	{
		return new ElasticEaseIn(a, p);
	}

	/**
	 *
	 * @param	a	Specifies the amplitude of the sine wave.
	 * @param	p	Specifies the period of the sine wave.
	 * @return	An easing with passed parameters.
	 */
	static easeOutWith(a = 0, p = 0)
	{
		return new ElasticEaseOut(a, p);
	}

	/**
	 *
	 * @param	a	Specifies the amplitude of the sine wave.
	 * @param	p	Specifies the period of the sine wave.
	 * @return	An easing with passed parameters.
	 */
	static easeInOutWith(a = 0, p = 0)
	{
		return new ElasticEaseInOut(a, p);
	}

	/**
	 *
	 * @param	a	Specifies the amplitude of the sine wave.
	 * @param	p	Specifies the period of the sine wave.
	 * @return	An easing with passed parameters.
	 */
	static easeOutInWith(a = 0, p = 0)
	{
		return new ElasticEaseOutIn(a, p);
	}
}